import React, { useState } from "react";
import Slider from "react-slick";

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import guideCover from "../images/guide_cover.jpg";

const HomeGuide = ({ guide }) => {
  const [visible, setVisible] = useState();
  var guideSettings = {
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    infinite: false,
  };
  return (
    <>
      <div className="home_guide_block">
        <div className="container">
          <Slider {...guideSettings}>
            {guide.length > 0 &&
              guide.map((item, index) => {
                return (
                  <div
                    className="home_guide_area animate__animated animate__slideInRight"
                    key={index}
                  >
                    <div
                      dangerouslySetInnerHTML={{ __html: item.guideContent }}
                    />
                    <button
                      type="button"
                      aria-label="Download"
                      className="guide_download"
                      onClick={() => setVisible(true)}
                    >
                      Download Buying Guide
                    </button>
                  </div>
                );
              })}
          </Slider>
        </div>
      </div>
      {visible && (
        <div className="home_guide_popup">
          <div className="home_guide_popup_content">
            <button
              type="button"
              aria-label="close"
              className="guide_close"
              onClick={() => setVisible(false)}
            />
            <div className="home_guide_popup_left">
              <img
                src={guideCover}
                alt="Propey Guide"
                width={300}
                height={450}
              />
            </div>
            <div className="home_guide_popup_right">
              <h3>Enter Your Details</h3>
              <form>
                <div className="form_field">
                  <input type="text" placeholder="Name" required />
                </div>
                <div className="form_field">
                  <input type="email" placeholder="Email" required />
                </div>
                <div className="form_field">
                  <input type="tel" placeholder="Phone" required />
                </div>
                <button
                  type="submit"
                  aria-label="Download"
                  className="guide_download"
                >
                  Download Buying Guide
                </button>
              </form>
            </div>
          </div>
          <button
            type="button"
            aria-label="popup close"
            className="home_guide_popup_overlay"
            onClick={() => setVisible(false)}
          />
        </div>
      )}
    </>
  );
};

export default HomeGuide;
